/* about
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
p-about
==============================================================*/
.p-about {
    &__list {
        margin-top: rem(36);

        @include responsive(md) {
            margin-top: rem(86);
        }
    }

    &__item {
        background: $color-background;
        padding: rem(14) rem(12);

        @include responsive(md) {
            padding: rem(30) rem(80);
        }
    }

    &__item:not(:first-child) {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(35);
        }
    }

    &__titleWrapper {
        display: flex;
        align-items: center;
    }

    &__title {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(16 / 16);
        width: fit-content;

        @include responsive(md) {
            font-size: rem(20);
            line-height: calc(20 / 20);
        }
    }

    &__num {
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(16 / 16);
        border: 1px solid $color-text;
        border-radius: 50%;
        width: rem(32);
        height: rem(32);
        margin-right: rem(14);

        @include responsive(md) {
            font-size: rem(18);
            width: rem(48);
            height: rem(48);
            margin-right: rem(21);
        }
    }

    &__text {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);
        margin-top: rem(20);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(24 / 16);
            margin-top: rem(35);
        }
    }
}

/*==============================================================
p-about-message
==============================================================*/
.p-about-message {
    &__heading {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 12);

        @include responsive(md) {
            font-size: rem(32);
            line-height: calc(48 / 32);
        }
    }

    &__box--left {
    }

    .p-media__img {
    }

    &__box--right {
        margin-top: rem(30);

        figure {
            margin-top: rem(10);

            @include responsive(md) {
                margin-top: rem(21);
            }
        }
    }

    &__text {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(32 / 16);
        }
    }

    &__text:not(:first-of-type) {
        margin-top: rem(20);

        @include responsive(md) {
            margin-bottom: rem(31);
        }
    }
}

/*==============================================================
p-about-business
==============================================================*/
.p-about-business {
    margin-top: rem(20);

    @include responsive(md) {
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
    }

    &__head {
        color: $color-accent;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(30 / 20);

        @include responsive(md) {
            font-size: rem(32);
            line-height: calc(32 / 32);
        }
    }

    &__intro {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(18);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(24 / 18);
        margin-top: rem(16);

        @include responsive(md) {
            font-size: rem(24);
            line-height: calc(24 / 24);
            margin-top: rem(40);
        }
    }

    &__txt {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);
        margin-top: rem(16);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(24 / 16);
            margin-top: rem(40);
        }
    }
}

