/* container
----------------------------------------- */
@use "../global" as *;

.l-container {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 rem(20);

  @include responsive(md) {
    padding: 0 rem(80);
  }
}

.p-about {
  .l-container {
    padding-top: rem(40);

    @include responsive(md) {
      padding-top: rem(66);
    }
  }
}

.p-about-message {
  .l-container {
    padding-top: rem(40);

    @include responsive(md) {
      margin-top: rem(125);
    }
  }
}

.p-about-business {
  .l-container {
    padding-top: rem(40);

    @include responsive(md) {
      margin-top: rem(111);
    }
  }
}
