/* hero
----------------------------------------- */
@use "../../global" as *;

.p-hero {
    margin-top: rem(60);
    position: relative;

    @include responsive(xxl) {
        margin-top: rem(102);
    }

    figure {
        height: rem(280);

        @include responsive(lg) {
            height: initial;
        }
    }

    figure {
        picture {
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
